import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps, ButtonNavigation, ButtonNavigationItem, Icon, Content, Swiper, Card, CardHeader, CardContent, CardFooter
    , Button, Avatar, MessageBox, AccordionGroup, Accordion, Modal, FabButton, ActionSheet} from 'bee-mobile';
import './App.scss';

interface ButtonNavigationCaseState {
    index: number;
}

export default class App extends React.PureComponent {

    state: ButtonNavigationCaseState = {
        index: 0,
        visible: false
    };
    handleChange = (event: any) => {
        this.setState({
            index: event.index
        });
    };

    handleOpen = () => {
        this.setState({
            visible: true
        });
    }

    handleClose = () => {
        this.setState({
            visible: false
        });
    };

    handleClick = () => {
        ActionSheet.show({
            buttons: [{
                text: 'Button1'
            }, {
                text: 'Button2'
            }],
            cancel: true
        })
    };

	render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'ActionSheetCase',
            'ModalCase',
            'CardCase',
            'ButtonNavigationCase',
            className
        );
		return (
            <Content style={{transform: 'translate3d(0, 0)'}} className={styleClass} {...other}>
                <Swiper touch={false} pagination={false} activeIndex={this.state.index}>
                    <div className="box padding-10">
                        <Card>
                            <CardHeader
                                avatar={<Avatar src="react-mobile-demo/images/head1.jpeg"/>}
                                title="绝地刚枪王"
                                subTitle="送给犊哥的小嘻嘻"
                            />
                            <CardContent>
                                <Avatar src="react-mobile-demo/images/img3.jpg"/>
                                <div className="padding-10">
                                    准备好了吗
                                </div>
                            </CardContent>
                            <CardFooter className="padding-5">
                                <Button flat={true}>Share</Button>
                            </CardFooter>
                        </Card>
                        <AccordionGroup className="bm-paper-2dp" closeOthers={true}>
                            <Accordion header="我想对你说">
                                <Avatar src="react-mobile-demo/images/img1.jpg"/>
                            </Accordion>
                            <Accordion header="哈哈">
                                <Avatar src="react-mobile-demo/images/img4.jpg"/>
                            </Accordion>
                        </AccordionGroup>
                    </div>
                    <div className="CasePanel">
                        <Button className='page2-btn' theme="primary" onClick={this.handleOpen}>准备好了吗</Button>
                        <Modal header="来来来" visible={this.state.visible} onClose={this.handleClose}>
                            <div className="padding-10">
                                大吉大利,晚上吃鸡
                                <Avatar src="react-mobile-demo/images/gif1.gif"/>
                            </div>
                        </Modal>
                    </div>
                    <div className="box padding-10">
                        <div className="CasePanel">
                            <FabButton position='center' type="circle" icon="😘" reverse={true}>
                                <Icon icon="😙" />
                                <Icon icon="😀" />
                                <Icon icon="😈" />
                                <Icon icon="💖" />
                            </FabButton>
                        </div>
                    </div>
                </Swiper>
                <ButtonNavigation onChange={this.handleChange}>
                    <ButtonNavigationItem label="Recents" icon={<Icon icon="home"/>} />
                    <ButtonNavigationItem label="Nearby" icon={<Icon icon="accessibility"/>} />
                    <ButtonNavigationItem label="Favorites" icon={<Icon icon="extension"/>} />
                </ButtonNavigation>
            </Content>
		);
	}
}



